.whiteboard {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.toolbar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  img {
    width: 15px;
    vertical-align: middle;
  }

  input {
    margin: 0 2px;
  }

  input:not([type='checkbox']) {
    width: 50px;
  }

  :global(.p-button) {
    margin: 0 1px;
  }
}

canvas {
  border: 1px solid rgb(165, 165, 165);
  border-radius: 3px;
}

:global(button) {
  padding: 12px;
  margin: 1px;
  background-color: #7b7de0;
  border-radius: 3px;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;

  &:hover {
    background-color: #7173ee;
  }
}

.dropdownButton {
  background-color: #7b7de0;
  color: white;
  padding: 11px 14px;
  font-size: 14px;
  border: none;
}

.uploadDropdown {
  position: relative;
  display: inline-block;

  input {
    display: none;
  }
}

.dropdownContent {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdownContent span {
  display: block;
  color: black;
  padding: 12px 16px;
  cursor: pointer;
}

.dropdownContent a:hover {
  background-color: #ddd;
}

.uploadDropdown:hover .dropdownContent {
  display: block;
}

.uploadDropdown:hover .dropdownButton {
  background-color: #7173ee;
}
